.iframe-container {
    position: relative;
    width: 100%;
    padding-bottom: 56.25%;
    height: 0;
}

.iframe-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.responsive {
    padding-left: 10%;
    padding-right: 10%;
    width: 80%;
    height: auto;
}

.responsive-avatar {
    padding-left: 20%;
    padding-right: 20%;
    width: 60%;
    height: auto;
}